@import './default.less';

/*  */
// 
.gm-text {
  color: @text-color;
  font-weight: 400;
  font-size: 14px;
}

// 
.gm-second-text {
  .gm-text;

  color: @text-color-secondary;
}

// 
.gm-disabled-text {
  .gm-text;

  color: @disabled-color;
}

// 
.gm-error-text {
  .gm-text;

  color: @error-color;
}

// 
.gm-label-text {
  .gm-text;

  color: #697b8c;
}
